<template>

<div class="hello">
  <div class="navbar navbar-dark bg-dark shadow-sm">
    <div class="container">
      <a href="#" class="navbar-brand d-flex align-items-center">
        <strong>Circus Family</strong>
      </a>
    </div>
  </div>

  <div class="row py-lg-5">
      <div class="col-lg-6 col-md-8 mx-auto">
        <h1 class="font-weight-light">Domotica Control</h1>
        <p class="lead text-muted">Via this page you can transmit and receive OSC data.</p>
        <p>
  
        </p>
      </div>
  </div>

  <div class="album py-5 bg-light">
    <div class="transmit">
      <h3>Transmit OSC data</h3>
      <a href="#" class="btn btn-primary">Turn something on</a>

      <br>
      <div class="wrapper">
        <input type="range" min="0" max="100" step="1" v-model="value" v-on:input="sendSlider" > 
        <span v-text="total"></span>
      </div>

      
    </div>
    <div class="receive">
      <h3>Receive OSC data</h3>

       <div id="messageArea">
            <p id="messageLabel">Remote OSC Message:</p>
            <p>{{OSCmessage()}}</p>
               <span v-text="OSCMessage"></span>
            <p>{{message}}</p>
            <pre id="message"></pre>
        </div>
    </div>
    <div class="container">

    </div>
  </div>
  <div class="text-muted py-5">
  <div class="container">
    <p class="mb-1">Domotica control is &copy; Circus family</p>
  </div>
</div>
<button v-on:click="greet">send</button>
</div>
  
</template>
<script src="../../node_modules/osc/dist/osc-browser.min.js"></script>
<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.3.1/jquery.min.js"></script>

            
<script>
import osc from "osc";
import $ from 'jquery'

 var port = new osc.WebSocketPort({
          url: "ws://178.62.209.37:8083" //localhost:8083 online server 178.62.209.37:8083
        });


   port.on("message", function (oscMessage) {
         $("#message").text(JSON.stringify(oscMessage,["address","args"]));
         //console.log("message", oscMessage);
      });;

 port.open();




export default {
  

  data() {
  return{
    value: 60,
    message: ''
     
    
  }},
  computed: {
       OSCMessage: function(){

        

    return this.message

    },
    
   
    total: function () {
        
      return this.value 
    }
  },
  methods:{
    greet: function(){
      
      port.send({
        address: "/hello",
          args: ["De slider werkt!"]
         });
    },
      sendSlider: function(){

        port.send({
        address: "/hello",
          args: [this.value]
         });

     },
      OSCmessage: function(){
    },
   
  }
  
}


</script>

